.table-input{
    width: 55px;
    border-radius: 5px;
    border: 1px solid darkgray;
    font-size: 1.1em;
    padding: 10px;
    height: 50px;
}
.table-input:hover{
    border: 1px solid rgb(45, 45, 45);
}
table{
    border: 1px solid darkgray;
    border-radius: 5px;
    /* margin: 10px 0;  */
    /* padding: 5px; */
    /* padding-bottom: 12px !important; */
}
.popupdiv{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 200;
    width: 100%;
    backdrop-filter: blur(2px);
}
.popupdiv > div{
    border: 1px solid lightgrey;
    background-color: white;
    box-shadow: 2px 2px 15px grey;
    border-radius: 10px;
    padding: 15px;
}
.table-cell{
    height: 20px;
    width: 20px;
    cursor: pointer;
    border: 1px solid darkgray;
    border-radius: 2px;
}
.center-div{
    display: flex;
    align-items: center;
    justify-content: center;
}
.submit-btn{
    display: flex;
    flex-direction: row-reverse;
}
.more-btn-div{
    display: flex;
    justify-content: end;
    align-items: center;
    margin-top: 5px;
}

@media screen and (max-width: 600px){
    .center-div{
        display: block;
        margin-right: 200px !important;
    }
    .small-container{
        width: 300px !important;
    }
    .table-cell{
        height: 10px;
        width: 10px;
    }
}